// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'

// css
import './assets/css/reset.css'

// 
import MuseUI from 'muse-ui' // Material Design UI 
import 'muse-ui/dist/muse-ui.css'
// import theme from 'muse-ui/lib/theme'
// import 'material-design-icons/iconfont/material-icons.css' // Muse-UI  material-icons
import VueAwesomeSwiper from 'vue-awesome-swiper' // 
import 'swiper/dist/css/swiper.css' // 
import VueLazyload from 'vue-lazyload' // 

// FontAwesome
import { library } from '@fortawesome/fontawesome-svg-core'
import { faListUl, faStepForward, faPlay, faPause } from '@fortawesome/free-solid-svg-icons'
import { faPlayCircle, faHeart, faBell, faPauseCircle } from '@fortawesome/free-regular-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

// mock
import './mock'

// 
import Banner from './components/Banner' // banner
import List from './components/List' // 
import MusicBar from './components/MusicBar' // 

Vue.config.productionTip = false

Vue.use(MuseUI)
// theme.use('light')
Vue.use(VueAwesomeSwiper)
Vue.use(VueLazyload)

Vue.component('Banner', Banner)
Vue.component('List', List)
Vue.component('MusicBar', MusicBar)
library.add(faPlayCircle, faHeart, faBell, faListUl, faPauseCircle, faStepForward, faPlay, faPause)
Vue.component('font-awesome-icon', FontAwesomeIcon)

// 
Vue.filter('secToDate', t => {
  //  00:00
  if (t || t < 1) {
    // Math.floor x
    // Math.round 
    var m = Math.floor(t / 60 % 60)
    var s = Math.floor(t % 60)
    m = m < 10 ? '0' + m : m
    s = s < 10 ? '0' + s : s
    return m + ':' + s
  } else {
    return t
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



